import React from 'react';
import BannerHome from '../component/BannerHome';
import { useSelector } from 'react-redux';
import HorizontalScrollCard from '../component/HorizontalScrollCard';
import useFetch from '../hooks/useFetch';

const Home = () => {
  const trendingData = useSelector(state => state.movieoData.bannerData);
  const { data: nowPlayingData } = useFetch('/movie/now_playing');
  const { data: topRated } = useFetch('/movie/upcoming');
  const { data: popularTvShows } = useFetch('/tv/popular');
  const { data: onAirShows } = useFetch('/tv/top_rated');

  return (
    <div>
      <BannerHome />
      <HorizontalScrollCard data={trendingData} heading="Trending Movies" trending={true} />
      <HorizontalScrollCard data={nowPlayingData} heading="Now Playing" media_type="movies" />
      <HorizontalScrollCard data={topRated} heading="Top Rated Movies" media_type="movies" />
      <HorizontalScrollCard data={popularTvShows} heading="Popular TV Shows" media_type="tv" />
      <HorizontalScrollCard data={onAirShows} heading="On the Air" media_type="tv" />
    </div>
  );
};

export default Home;
