import { useState, useEffect, useCallback } from "react";
import axios from "axios";

const useFetch = (endpoint) => {
    const [data, setData] = useState([]);             // State to store fetched data
    const [loading, setLoading] = useState(false);     // State to track loading status
    const [error, setError] = useState(null);          // State to track errors

    const fetchData = useCallback(async () => {
        if (!endpoint) {
            console.warn("No endpoint provided to fetch data.");
            return; // Exit early if no endpoint is provided
        }

        setLoading(true);
        setError(null); // Reset error before fetching data

        try {
            const response = await axios.get(endpoint);
            setData(response.data.results); // Set fetched data
        } catch (err) {
            setError(err); // Capture error
            console.error('Error fetching data:', err.message || err);
        } finally {
            setLoading(false); // Stop loading regardless of success or failure
        }
    }, [endpoint]);

    useEffect(() => {
        fetchData(); // Fetch data when component mounts or endpoint changes
    }, [fetchData]); // Dependency on fetchData

    return { data, loading, error }; // Return data, loading, and error state
};

export default useFetch;
